body {
  background: white;
  color: #212326;
  font-family: ShopifySans, Helvetica, Arial, sans-serif;
}

.movie-data {
  width: fit-content;
}

.movie-app > .row {
  overflow-x: auto;
  overflow-y: hidden;
  flex-wrap: nowrap;
}

.image-container {
  width: fit-content;
  position: relative;
  transition: transform 0.2s;
}

.image-container:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  background-color: black;
  color: white;
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}

.add-text {
  margin-right: 10px;
}

.remove-text {
  margin-right: 10px;
}
